import React from "react";
import "../stylesheets/main.css";
import { CheckCircleSolid, TransitionRightSolid } from "iconoir-react"

const CategoryElement = ({ category, text }) => (
  <li className="categorys-list-element">
    <span className="checkbox"><CheckCircleSolid color="#F2661F" /></span>
    <p> <span className="checkbox">{category}</span>{text}</p>
  </li>
)

const Services = ({ classCard, classText, text }) => (
  <a href="/formularioMicro" className="service">
    <div
      className={classCard}>
      <div className="overlay"></div>
    </div>
    <div className={classText}>
      <h4>{text}</h4>
    </div>
  </a>
)

function MainContent() {
  return (
    <main className="main">
      <div className="main-content">

        <section className="section-title">
          <div>
            <h1 className="title">Bienvenidos a nuestra herramienta de autodiagnstico</h1>
            <h4 className="description-title">Descubre tu nivel de madurez digital</h4>
          </div>
        </section>

        <div className="tools-information">
          <section className="section-content">
            <div className="section-content-text">
              <p>
                Bienvenidos a nuestra herramienta de autodiagnstico del nivel de madurez en la transformacin digital desarrollada por la Cmara de Comercio de Cali y Wirk Consulting SAS. Esta herramienta est diseada para ayudar a las empresas a evaluar su nivel de madurez digital en diferentes reas clave, y proporcionar recomendaciones para mejorar su capacidad de transformacin digital.</p>
              <p>
                La transformacin digital se ha convertido en una necesidad para todas las empresas en la actualidad. La implementacin de tecnologas digitales y la adopcin de nuevas formas de trabajar pueden ofrecer a las empresas ventajas competitivas y mejorar su capacidad de satisfacer las necesidades de los clientes y del mercado.
              </p>
              <p>
                Sin embargo, la transformacin digital no es un proceso fcil y puede presentar desafos para las empresas. Por esta razn, es importante que las empresas comprendan su nivel de madurez digital actual y tengan una visin clara de las reas que deben mejorar para lograr una transformacin digital exitosa.
              </p>
              <p>
                Nuestra herramienta de diagnstico del nivel de madurez en la transformacin digital de medianas y grandes empresas es muy completa y profunda, ya que evala las dimensiones clave que deben ser consideradas para una transformacin digital exitosa.
              </p>
              <p>
                Las dimensiones a evaluar son:
              </p>
            </div>

            <section className="categorys">
              <ul className="categorys-list">
                <CategoryElement
                  category={"Estrategia digital:"}
                  text={"Evala si la empresa tiene una estrategia clara y coherente para la transformacin digital, si la estrategia est alineada con los objetivos de negocio y si se estn tomando medidas para implementar la estrategia."}
                />
                <CategoryElement
                  category={"Cultura digital:"}
                  text={"Evala si la empresa tiene una cultura digital slida y una mentalidad de innovacin, si los empleados estn comprometidos con la transformacin digital y si la empresa est promoviendo la colaboracin y el aprendizaje continuo."}
                />
                <CategoryElement
                  category={"Capacidades digitales:"}
                  text={"Evala si la empresa tiene las habilidades y el talento necesarios para llevar a cabo la transformacin digital, si se estn invirtiendo en la formacin y el desarrollo de los empleados y si la empresa tiene una estructura organizativa adecuada para la transformacin digital."}
                />
                <CategoryElement
                  category={"Experiencia del cliente:"}
                  text={"Evala si la empresa est utilizando la tecnologa para mejorar la experiencia del cliente, si se estn adoptando nuevas tecnologas para mejorar la interaccin con los clientes y si se estn midiendo los resultados y tomando medidas para mejorar continuamente."} />
              </ul>
            </section>
          </section>

          <section className="categorys">
            <ul className="categorys-list">
              <CategoryElement
                category={"Innovacin y colaboracin:"}
                text={"Evala si la empresa est fomentando la innovacin y la colaboracin, si se estn implementando prcticas giles para el desarrollo de productos y servicios, si se estn adoptando nuevas tecnologas y si se estn explorando nuevas formas de colaboracin con socios y proveedores."}
              />
              <CategoryElement
                category={"Gobierno digital:"}
                text={"Evala si la empresa tiene una estructura de gobierno adecuada para la transformacin digital, si se estn estableciendo polticas y normas claras para el uso de la tecnologa y si se estn tomando medidas para mitigar los riesgos asociados con la transformacin digital"} />
              <CategoryElement
                category={"Tecnologas digitales emergentes:"}
                text={"Evala si la empresa est adoptando tecnologas emergentes como la inteligencia artificial, la nube, el blockchain y la robtica, si se estn explorando nuevas oportunidades de negocio y si se estn preparando para los desafos que pueden presentarse."} />
              <CategoryElement
                category={"Resultados de negocio:"}
                text={"Evala si la empresa est logrando resultados positivos a travs de la transformacin digital, si se estn midiendo los resultados y si se estn tomando medidas para mejorar continuamente."} />
            </ul>
            <div className="conclusion-container">
              <p>
                Al evaluar estas dimensiones, nuestra herramienta proporciona una imagen completa del nivel de madurez digital de la empresa y ayuda a identificar las reas en las que se pueden realizar mejoras. Esperamos que esta herramienta sea til para todas las empresas que estn interesadas en mejorar su capacidad de transformacin digital.
              </p>
            </div>
          </section>
        </div>

        <div className="options">
          <div className="options-title">
            <h4>Explora entre</h4>
            <h2>nuestras opciones</h2>
          </div>
          <hr className="options-line" />
        </div>

        <section className="services-container">
          <Services
            classCard={'service-image bg-1'}
            classText={'service-type hov-b'}
            text={'medianas y grandes empresas'}
          />
          <Services
            classCard={'service-image bg-2'}
            classText={'service-type hov-b'}
            text={'micros y pequeas empresas'}
          />
          <Services
            classCard={'service-image bg-3'}
            classText={'service-type hov-g'}
            text={'economa popular, asociativa y comunitaria'}
          />
          <Services
            classCard={'service-image bg-4'}
            classText={'service-type hov-aq'}
            text={'excelencia clnica'}
          />
        </section>

        <div className="more-information-container">
          <a href="https://www.ccc.org.co/" className="more-information">
            <span className="more-information-text">
              conoce ms sobre cmara de comercio de cali
            </span><TransitionRightSolid />
          </a>
        </div>
      </div>
    </main>
  );
}

export default MainContent;